@charset "UTF-8";
/* home page */
@font-face {
  font-family: "Besley";
  font-display: swap;
  src: url("/_assets/fonts/Besley/Besley-VariableFont_wght.woff2") format("woff2"), url("/_assets/fonts/Besley/Besley.woff") format("woff"), url("/_assets/fonts/Besley/Besley-VariableFont_wght.ttf") format("truetype");
}
@font-face {
  font-family: "Inconsolata";
  font-display: swap;
  src: url("/_assets/fonts/Inconsolata/Inconsolata-VariableFont_wdth-wght.woff2") format("woff2"), url("/_assets/fonts/Inconsolata/Inconsolata.woff") format("woff"), url("/_assets/fonts/Inconsolata/Inconsolata-VariableFont_wdth-wght.ttf") format("truetype");
}
@font-face {
  font-family: "Instrument Sans";
  font-display: swap;
  src: url("/_assets/fonts/Instrument_Sans/InstrumentSans-VariableFont_wdth-wght.woff2") format("woff2"), url("/_assets/fonts/Instrument_Sans/InstrumentSans.woff") format("woff"), url("/_assets/fonts/Instrument_Sans/InstrumentSans-VariableFont_wdth-wght.ttf") format("truetype");
}
:root {
  --font-1: "Instrument Sans", sans-serif;
  --font-2: "Besley", serif;
  --font-3: "Inconsolata", monospace;
  --font-color-light: #3b0c3b;
  --h1-light: #5a5463;
  --h2-light: #712357;
  --h3-light: #631863;
  --link-color-light: darkmagenta;
  --link-hover-bg-light: rgb(216, 112, 147);
  --document-color-light: thistle;
  --page-color-light: #fff6f6;
  --section-card-light: rgb(255, 239, 239);
  --site-updates-bg-light: #fff5f5;
  --time-bg: lavender;
  --time-font-color: #685c7c;
  --time-border-bottom: 5px outset rgb(199, 199, 236);
  --time-border-right: 5px outset rgb(242, 242, 255);
  --last-played-bg: #b8dce0;
  --last-played-border-right: 10px outset rgb(189, 235, 232);
  --last-played-border-bottom: 10px outset rgb(155, 211, 207);
  --last-played-shadow: 10px 10px rgba(98, 136, 194, 0.5);
  --last-played-outline: 1px solid rgb(125, 143, 137);
  --web-rings-card: #f5ddff;
  --web-rings-font: rgb(92, 39, 92);
  --web-rings-links: rgb(122, 78, 180);
  --web-rings-bg-light: rgb(255, 233, 251);
  --footer-font: rgb(97, 58, 97);
  --overflow-bg-light: rgb(255, 244, 246);
  --overflow-border: 1px solid rgb(170, 99, 111);
}

/* animations */
body {
  opacity: 0;
  animation: fadeInPage 0.5s;
  animation-fill-mode: forwards;
}

.notice-group {
  animation: fadeInSlide 0.75s forwards;
}

.page-columns > section, #notice, .notice-group button {
  transition: transform 0.3s;
}

.page-columns > section:hover, #notice:hover, .notice-group button:hover {
  transform: translatey(-5px);
}

@keyframes fadeInSlide {
  0% {
    opacity: 0;
    transform: translatey(0);
  }
  100% {
    opacity: 1;
    transform: translatey(5px);
  }
}
@keyframes fadeInPage {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
* {
  scrollbar-color: pink rgba(201, 134, 154, 0.116);
  scrollbar-width: thin;
}

html {
  scrollbar-color: rgb(184, 140, 184) transparent;
}
html ::-webkit-scrollbar-thumb {
  background-color: rgb(173, 106, 173);
}
html ::-webkit-scrollbar-thumb:hover {
  background-color: #814c91;
}

body {
  font-family: var(--font-1);
  font-size: 1em;
  font-weight: 500;
  line-height: 1.5em;
  letter-spacing: 0.005em;
  margin: auto;
}
body ::-webkit-scrollbar-thumb {
  background-color: pink;
}
body ::-webkit-scrollbar-thumb:hover {
  background-color: plum;
}

main {
  margin: auto;
}
main a {
  font-weight: 600;
}

.board {
  border: 1px solid purple;
  background-color: #fff6f6;
  box-shadow: 5px 5px 10px rgba(128, 0, 128, 0.1);
  margin-top: 5%;
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
  height: fit-content;
}

header {
  background-color: inherit;
  position: sticky;
  top: 0;
  z-index: 1;
}
header .top {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  margin-left: 5%;
  margin-right: 5%;
  padding-top: 3vh;
  padding-bottom: 3vh;
}
header hgroup p {
  font-family: var(--font-2);
  font-size: 1.5em;
  letter-spacing: 0.05em;
  margin-top: 0;
  opacity: 0.9;
}
header .ascii {
  font-weight: 100;
  color: rgb(182, 149, 182);
  user-select: none;
  white-space: pre;
}
header .ascii-img {
  vertical-align: middle;
  user-select: none;
}

h1 {
  font-size: 2.5em;
  font-family: var(--font-2);
  font-weight: 500;
  letter-spacing: 0.05em;
  margin-bottom: 0;
}

nav {
  margin: auto;
  padding-left: 1em;
  padding-right: 1em;
  border-bottom: 1px solid gray;
  border-top: 1px solid gray;
  font-size: 1.15em;
  letter-spacing: 1px;
}
nav ul {
  margin: 0;
  padding: 0;
  padding-top: 0.5em;
  padding-bottom: 1em;
}
nav li {
  display: inline-block;
  margin-right: 0.5em;
  margin-bottom: 0;
  padding-bottom: 0;
  white-space: nowrap;
}
nav li:last-child::after {
  content: "âœ§";
  font-size: 1.3em;
  margin-left: 0.5em;
  color: pink;
  font-weight: normal;
}
nav li::before {
  content: "âœ§";
  font-size: 1.3em;
  margin-right: 0.5em;
  color: pink;
  font-weight: normal;
}
nav ul li:first-child {
  font-weight: 600;
}
nav a {
  text-decoration: none;
  color: purple;
  padding: 0px 5px 3px 5px;
}
nav #home a {
  text-decoration: wavy underline 1px;
}
nav::-webkit-scrollbar-thumb {
  background-color: rgba(161, 117, 197, 0.4509803922);
}

.page-columns {
  columns: 3;
  column-gap: 0;
  background-image: url("/_assets/media/main/ooorganize.svg");
  background-size: 200px !important;
  padding-top: 5vh;
  padding-bottom: 5%;
  padding-left: 1%;
  padding-right: 1%;
}

h2 {
  margin: 0;
  border-bottom: 1px solid rgb(233, 166, 205);
  padding-bottom: 0.5em;
  font-size: 1.25em;
  letter-spacing: 0.05em;
}
h2 [aria-hidden=true] {
  user-select: none;
}

h3 {
  font-weight: 600;
  margin-bottom: 0;
}

a {
  background-color: transparent;
  transition: all 0.2s ease-out;
  text-decoration-style: dashed;
  text-underline-offset: 5px;
  text-decoration-thickness: 1px;
}
a:hover {
  text-decoration: none;
  color: white !important;
}

.button-style {
  display: block;
  border: 1px solid;
  padding: 0.5em;
  font-family: var(--font-3);
}

button {
  cursor: pointer;
  transition: all 0.5s ease;
}

button:active, .button-style:active {
  transition: all 0.1s ease;
}

#themeSwitch {
  margin-top: auto;
  margin-bottom: auto;
  font-family: var(--font-3);
  font-size: 1em;
  height: fit-content;
  width: fit-content;
  padding: 0.5em;
  border: 1px solid #8e629b;
}

.page-columns > section {
  margin-left: auto;
  margin-right: auto;
  padding-top: 5px;
  padding-bottom: 10vh;
  break-inside: avoid;
}

.card {
  border: 1px solid rgb(190, 133, 141);
  border-radius: 15px 15px 15px 15px;
  padding: 1em;
  margin-left: auto;
  margin-right: auto;
  height: min-content;
  box-shadow: 10px 10px rgba(255, 149, 165, 0.5);
  break-inside: avoid;
  width: 70%;
}
.card ul {
  padding-left: 1em;
  padding-top: 1em;
  margin-top: 0;
}
.card li {
  margin-bottom: 1em;
}
.card button {
  padding: 1em;
  border: 1px solid;
  border-radius: 50px;
  font-family: var(--font-3);
}
.card footer {
  width: 100%;
  text-align: left;
  margin-top: 2em;
  padding: 1em 0px 1.5em;
  border-top: 1px dashed thistle;
}

.h-card {
  border: 1px solid rgb(183, 124, 189);
  background: url("/_assets/media/main/bank-note.svg");
  background-color: snow;
  box-shadow: 10px 10px rgba(192, 138, 176, 0.521);
  height: min-content;
  min-width: 250px;
  font-size: 1.1em;
}
.h-card [rel=me] {
  opacity: 0;
}

#webmaster-identity {
  border: none;
  font-variant: small-caps;
  letter-spacing: 1px;
  font-size: 1.5em;
}

.pfp-group {
  display: flex;
  width: fit-content;
  margin-top: 0;
}

.flower-fill {
  margin-left: 10px;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  user-select: none;
}
.flower-fill img:not(:last-child) {
  margin-bottom: 10px;
}

.u-featured {
  width: 290px !important;
  height: 341px !important;
}

.p-note {
  letter-spacing: 0.025em;
}
.p-note p {
  font-size: 1.1em;
  margin: 0.75em;
}
.p-note ul {
  width: 85%;
  font-size: 1.1em;
  list-style: none;
  padding: 0;
  padding-left: 0.5em;
  margin: 0;
  margin-bottom: 1.5em;
}
.p-note li {
  margin-bottom: 0.5em;
  display: flex;
  flex-direction: row;
}
.p-note li span:first-of-type {
  width: 1em;
  text-align: center;
  margin-right: 1em;
}
.p-note .hidden {
  display: none;
}

.barcode {
  background: url("/_assets/media/main/barcode.svg");
  background-repeat: no-repeat;
  background-position: right;
  height: 2em;
}

.overflow {
  max-height: 30vh;
  overflow-y: auto;
  border: var(--overflow-border);
  padding-right: 1em;
  padding-left: 1em;
  margin-top: 1em;
}

[aria-labelledby=note-to-visitors] p {
  padding-left: 1em;
  padding-right: 1em;
}

.last-played {
  max-width: 225px;
  font-size: 1.1em;
  outline: var(--last-played-outline);
  text-align: center;
  padding-bottom: 5px;
  border-top: none;
  border-left: none;
  box-shadow: var(--last-played-shadow) !important;
}
.last-played .button-style {
  font-size: smaller;
  border-radius: 5px;
  margin-left: 5px;
  padding: 0.75em 1em;
  border: 1px solid green;
}

#last-played {
  margin-left: 0.5em;
  margin-right: 0.5em;
  font-variant: small-caps;
  border-color: rgb(79, 170, 161);
  font-family: var(--font-1);
}

#track {
  padding-top: 0.5em;
  border: 1px solid rgb(113, 135, 160);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  font-family: var(--font-3);
  text-transform: lowercase;
  overflow-x: hidden;
}

#song {
  white-space: nowrap;
  transform: translateX(0);
  animation: scroll 15s linear infinite;
  font-weight: 500;
}

@keyframes scroll {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-150%);
  }
}
@media (prefers-reduced-motion) {
  #song {
    white-space: normal;
    animation-play-state: paused;
  }
}
.last-played-child {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;
  padding: 15px 0px;
  border: 1px solid rgb(146, 162, 180);
  border-top: none;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  background-color: inherit;
}

#js-toggle {
  padding: 0.6em 1em;
  font: inherit;
  font-weight: 600;
  font-stretch: semi-expanded;
  border: 1px solid green;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.latest-status {
  width: 80%;
  padding: 0;
  margin: auto;
  border: 1px solid rgb(175, 140, 175);
  border-radius: 0;
  box-shadow: 10px 10px rgba(161, 110, 172, 0.493);
}

#latest-status {
  border: none;
  padding: 1em 1em 0.5em 1em;
}

#statuscafe {
  padding: 1em;
  margin-left: auto;
  margin-right: auto;
  border-top: 1px solid rgb(194, 167, 194);
  font-size: 1.1em;
}

#statuscafe-username {
  margin-bottom: 0.5em;
  font-weight: 600;
  min-height: 1em;
}

#statuscafe-content {
  padding: 0.5em;
  font-family: var(--font-3);
  min-height: 50px;
  word-wrap: break-word;
}

#statuscafe-content::before, #statuscafe-content::after {
  content: '"';
}

#statuscafe-content + * {
  margin: auto;
  margin-top: 1.5em;
  text-align: center;
}

.time {
  border-radius: 10px;
  text-align: center;
  width: 60%;
  max-width: 240px;
  margin: auto;
  padding: 0.25em 1em 0.25em;
  background-color: var(--time-bg);
  box-shadow: 10px 10px rgba(147, 112, 219, 0.5);
  border-bottom: var(--time-border-bottom);
  border-right: var(--time-border-right);
  outline: 1px solid rgb(140, 112, 169);
  color: var(--time-font-color);
  break-inside: avoid;
  font-size: 1.1em;
}

.p-locality {
  font-size: 1.3em;
}

.clock-display {
  color: mintcream;
  background-color: #374048;
  border-top: 3px inset gray;
  border-left: 3px inset gray;
  min-height: 50px;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 1.5em;
}

.clock {
  font-size: 3em;
  letter-spacing: 0.1em;
  margin: 0;
}

#tip-jar + ul {
  list-style: none;
  text-align: center;
  padding-left: 0;
}
#tip-jar + ul .button-style {
  border-radius: 10px;
  font-size: 1.25em;
  width: 80%;
  margin: auto;
  margin-top: 0.5em;
}

.web-rings {
  background-color: var(--web-rings-card);
  border-color: purple;
  color: var(--web-rings-font);
  box-shadow: 10px 10px rgba(141, 67, 141, 0.5);
  min-width: 275px;
}
.web-rings details p {
  text-align: left;
  position: absolute;
  border: 1px solid;
  color: black;
  padding: 1em;
  margin-left: 2em;
  width: 200px;
}
.web-rings summary {
  font-weight: 600;
  margin: 1em;
}
.web-rings p {
  text-align: center;
}
.web-rings a {
  font-weight: 600;
  font-size: larger;
  display: inline-block;
  padding: 0.5em;
}

.string {
  margin-left: 0.75em;
}

.web-rings-list {
  border: 1px solid rgb(167, 95, 177);
  margin-left: auto;
  margin-right: auto;
  overflow: auto;
  max-height: 300px;
  scrollbar-color: rgb(171, 113, 189) rgba(167, 49, 167, 0.315);
  scrollbar-width: thin;
}
.web-rings-list h3 {
  text-align: center;
  padding: 0.75em;
  margin-top: 0;
  letter-spacing: 0.2em;
  font-stretch: expanded;
  font-weight: 600;
}
.web-rings-list p {
  margin: 0 auto;
  padding: 0.5em;
  border-bottom: 1px solid rgb(138, 85, 138);
}
.web-rings-list p:first-of-type {
  border-top: 1px solid rgb(138, 85, 138);
}
.web-rings-list img {
  image-rendering: pixelated;
}
.web-rings-list ::-webkit-scrollbar-thumb {
  background-color: rgb(155, 96, 155);
}
.web-rings-list ::-webkit-scrollbar-thumb:hover {
  background-color: purple;
}
.web-rings-list ::-webkit-scrollbar-track-piece {
  background-color: rgba(112, 23, 134, 0.281);
}

[aria-labelledby=pending-web-rings] {
  background-color: rgba(212, 82, 219, 0.1215686275);
}

[aria-labelledby=joined-web-rings] p:last-of-type {
  border-top: 1px solid rgb(138, 85, 138);
  border-bottom: none;
}

#resourceRing {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-bottom: 1em;
}
#resourceRing a {
  padding: 0;
}

#vocaring {
  font-family: var(--font-3);
  font-stretch: semi-expanded;
  border: 1px solid rgb(22, 128, 117);
  margin: 2em auto;
  padding-top: 1em;
  background-color: rgba(64, 224, 208, 0.178);
  width: 70%;
}
#vocaring .title {
  font-size: 1.5em;
  font-weight: 600;
  font-stretch: expanded;
}
#vocaring a {
  display: inline;
  padding: 0px;
}
#vocaring img {
  margin: 1em auto 0.5em;
}

footer {
  margin-top: 3em;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 2em;
  text-align: center;
  font-size: 1em;
  width: 50%;
  height: fit-content;
  max-width: 500px;
}
footer time {
  display: inline;
}
footer ul {
  list-style-type: none;
  padding-left: 0;
}
footer li {
  display: inline-block;
  margin-right: 15px;
}
footer li::before {
  content: "â¬©";
  font-size: 1.25em;
  margin-right: 15px;
}
footer li:nth-child(1)::before {
  content: none;
}
footer a img {
  text-decoration: none;
}

time {
  font-weight: 600;
}

hr {
  margin: auto;
  border: 1px dashed plum;
}

dd {
  margin-top: 0.5em;
  margin-bottom: 1em;
  margin-left: 1em;
}
dd [aria-hidden=true] {
  user-select: none;
}

.links dt::before {
  content: "ðŸ”—";
  margin-right: 0.5em;
}
.links dd {
  margin-left: 2em;
  margin-bottom: 1em;
}

::selection {
  background-color: rgb(248, 208, 170);
  color: black;
}

.notice-group {
  position: absolute;
  right: 0;
  margin: auto;
  z-index: 1;
  font-family: var(--font-3);
}
.notice-group button {
  background-color: transparent !important;
  color: inherit;
  border: none;
  font-size: 2em;
  z-index: 2;
  position: absolute;
  right: 0;
  top: 0;
}
.notice-group button:hover {
  background-color: transparent !important;
  transform: translatey(-5px);
}

#notice p:first-child {
  font-size: x-large;
}

@media (prefers-color-scheme: light) {
  :root {
    --sticky-note-bg: cornsilk !important;
    --sticky-note-font: rgb(78, 61, 19) !important;
  }
  body {
    color: var(--font-color-light);
    background-color: var(--document-color-light);
  }
  a {
    color: var(--link-color-light);
  }
  a:hover {
    background-color: var(--link-hover-bg-light);
  }
  .button-style:hover {
    color: purple;
  }
  h1, header p {
    color: var(--h1-light);
  }
  h2 {
    color: var(--h2-light);
  }
  .last-played {
    background-color: var(--last-played-bg);
    border-bottom: var(--last-played-border-bottom);
    border-right: var(--last-played-border-right);
  }
  #track {
    background-color: linen;
  }
  #statuscafe-content {
    background-color: rgb(243, 242, 247);
    color: rgb(24, 14, 39);
  }
  .overflow {
    background-color: var(--overflow-bg-light);
  }
}
::-webkit-scrollbar {
  width: 0.5em;
  height: 0.5em;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
}

::-webkit-scrollbar-track-piece {
  background-color: inherit;
}

@media all and (max-width: 1100px) {
  body {
    font-size: 1.2em;
  }
  .page-columns {
    columns: 2;
  }
  .page-columns > section {
    padding-bottom: 5vh;
  }
  .board {
    margin-top: 0;
  }
  header {
    position: relative;
  }
  #webmaster-identity {
    font-size: 1.25em;
  }
  .h-card {
    max-width: 300px;
  }
  .u-photo {
    width: 200px;
    height: 200px;
  }
  .time {
    min-width: 260px;
  }
  .clock-display {
    width: 80%;
  }
  .last-played {
    max-width: 270px;
  }
  .latest-status {
    max-width: 350px;
  }
  [aria-labelledby=note-to-visitors] {
    max-width: 450px;
  }
  [aria-labelledby=tip-jar] {
    max-width: 420px;
  }
}
@media all and (max-width: 900px) {
  .board {
    max-width: 700px;
    border-top: none;
  }
  h1 {
    line-height: 1.5em;
  }
  header .top {
    padding: 1vh;
  }
  header p {
    margin: 3vh auto;
  }
  .card {
    max-width: 275px;
  }
  .p-locality {
    font-size: 1em;
    font-weight: 600;
  }
}
@media all and (max-width: 768px) {
  .board {
    margin-top: 0;
    max-width: none;
    border-left: none;
    border-right: none;
  }
  header {
    position: relative;
    padding-top: 0;
  }
  header hgroup {
    margin-right: 20vw;
  }
  header hgroup p {
    font-size: clamp(1.5em, 3vw, 1.75em);
    line-height: 1.5em;
  }
  h1 {
    line-height: 1.75em;
  }
  nav {
    padding-left: 5%;
    overflow-x: auto;
    white-space: nowrap;
  }
  .notice-group {
