:root {
  --page-bg-dark: rgb(13, 13, 14);
  --page-border-dark: #503281;
  --section-card-dark: hsl(255, 5%, 16%);
  --dark-mode-links: rgb(255, 192, 216);
  --link-hover-bg-dark: rgba(216, 112, 147, .5);
  --dark-mode-h: rgb(223, 154, 236);
  --dark-button-bg: #463646;
  --last-played-dark-bg: rgb(63, 85, 85);
  --last-played-dark-r-border: 10px outset rgb(77, 90, 88);
  --last-played-dark-b-border: 10px outset rgb(40, 44, 44);
}

@media (prefers-color-scheme: dark) {
  :root {
    --sticky-note-bg: var(--section-card-dark);
  }
  body {
    color: white;
    background-color: black;
  }
  h1, header nav, #statuscafe-content, .web-rings, .time, .web-rings details p, footer {
    color: white;
  }
  header nav {
    border-color: #503281;
  }
  header nav a {
    color: var(--dark-mode-links);
  }
  h2, h3 {
    color: var(--dark-mode-h);
  }
  a, .web-rings a {
    color: var(--dark-mode-links);
  }
  a:hover {
    background-color: var(--link-hover-bg-dark);
  }
  button, .button-style {
    background-color: var(--dark-button-bg);
    color: rgb(230, 214, 236);
  }
  button:hover, .button-style:hover {
    background-color: #712357;
  }
  button:active, .button-style:active {
    background-color: rgb(207, 102, 102);
    color: white;
  }
  button:focus, .button-style:focus {
    background-color: #8b4f61;
  }
  .board {
    background-color: var(--page-bg-dark);
    border-color: var(--page-border-dark);
  }
  .page-columns {
    background: url("/_assets/media/main/ooorganize-dark.svg");
  }
  .card, #track, #statuscafe, .time, .web-rings, .web-rings details p, .web-rings-list {
    background-color: var(--section-card-dark);
    color: white;
  }
  .h-card {
    background-image: url("/_assets/media/main/bank-note-dark.svg");
    text-shadow: 0 0 5px black;
    background-color: var(--section-card-dark);
  }
  .time {
    border-bottom: 5px outset rgb(58, 58, 88);
    border-right: 5px outset rgb(67, 67, 102);
  }
  #latest-status {
    background: url("/_assets/media/main/line-in-motion-dark.svg") rgb(52, 39, 58);
  }
  #statuscafe-content {
    background-color: rgba(255, 255, 255, 0.1);
  }
  .last-played {
    background-color: var(--last-played-dark-bg);
    border-bottom: var(--last-played-dark-b-border);
    border-right: var(--last-played-dark-r-border);
  }
}
